/* Components */
.article {
  padding-top: 1.875rem;
}

@media only screen and (min-width: 1025px) {
  .article {
    padding-top: 3.75rem;
  }
}

.article__heading {
  text-align: center;
  display: block;
  margin-bottom: 3.75rem;
}

.article__content {
  padding-top: 2.625rem;
}

@media only screen and (min-width: 1025px) {
  .article__content {
    padding-top: 5.3125rem;
  }
}

.article__content p {
  font-size: 1rem;
  font-family: "Gotham", sans-serif;
  font-weight: 100;
  margin-bottom: 0;
  margin-top: 2.5rem;
}

@media only screen and (min-width: 1025px) {
  .article__content p {
    font-size: 1.375rem;
  }
}

.article__content p:first-child {
  margin-top: 0;
}

.article__content ul,
.article__content ol {
  padding-left: 1.25rem;
}

.article__content li {
  font-family: "Gotham", sans-serif;
  font-weight: 100;
  margin-bottom: 0;
  padding-bottom: 0.3125rem;
  font-size: 1rem;
  color: inherit;
}

@media only screen and (min-width: 1025px) {
  .article__content li {
    font-size: 1.375rem;
  }
}

@media only screen and (min-width: 1025px) {
  .article__main {
    max-width: 61.0625em;
    margin: 0 auto;
  }
}

.article__title {
  margin: 0;
  font-style: normal;
}

@media only screen and (max-width: 374px) {
  .article__title {
    font-size: 1.125rem;
  }
}

@media only screen and (min-width: 1025px) {
  .article__title {
    font-size: 2.5625rem;
  }
}

.article__date {
  margin-bottom: 2.1875rem;
  font-size: 1rem;
  font-family: courier;
  color: #8a8a8a;
}

.article__icons {
  display: flex;
  margin: 0 auto;
  max-width: 250px;
  padding-top: 4.0625rem;
  justify-content: space-between;
}

.article__item {
  font-size: 1rem !important;
}

.article__pagination {
  display: flex;
  justify-content: space-between;
  padding-top: 3.125rem;
  padding-bottom: 1.8125rem;
}

@media only screen and (min-width: 1025px) {
  .article__pagination {
    padding-bottom: 3.625rem;
    padding-top: 5.9375rem;
  }
}

.article__pagination-previous, .article__pagination-next {
  display: block;
  text-transform: uppercase;
  font-size: 0.6875rem;
  color: #636262;
  font-family: "Gotham", sans-serif;
  font-weight: 100;
}

@media only screen and (min-width: 1025px) {
  .article__pagination-previous, .article__pagination-next {
    font-size: 1.375rem;
  }
}

.article__pagination-previous {
  text-align: left;
}

.article__pagination-next {
  text-align: right;
}

.article__pagination--size {
  padding-top: 0.625rem;
  display: block;
  font-size: 0.9375rem;
  font-weight: 500;
  color: #636262;
}

@media only screen and (min-width: 1025px) {
  .article__pagination--size {
    font-size: 1.875rem;
  }
}

.blogs__heading {
  border-bottom: 4px solid #ed3fa8;
  padding-top: 3.125rem;
  padding-bottom: 1.875rem;
}

.blogs__heading h1 {
  color: #ed3fa8;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
}

@media only screen and (min-width: 1025px) {
  .blogs__heading {
    max-width: 61.0625em;
    margin: 0 auto;
  }
}

